<template>
  <b-container class="bg-light" fluid>
    <div class="mx-auto shadow-lg  text-center col-sm-8" v-if="!cartProducts.length">
      <b-card no-body bg-variant="info" class="my-5" text-variant="white">
        <template v-slot:header>
          <p class="text-center">
            <b-spinner type="grow" variant="warning" small class="mr-3"></b-spinner>Attention!!
            <b-spinner type="grow" variant="warning" small class="ml-3"></b-spinner>
          </p>
        </template>
        <b-card-body>
          <p class="text-center">There is no product in Bag</p>
          <p class="text-center">Enjoy shopping with Azraar</p>
        </b-card-body>
      </b-card>
    </div>
    <b-row>
      <b-col md="8" class="my-2">
        <b-container>
          <b-row class="my-2" v-if="cartProducts.length">
            <b-col class="align-self-center">
              <span style="cursor: pointer;">
                <b-icon v-if="allSelected" scale="1.1" icon="check-square" @click="handleAllDeselect"></b-icon>
                <b-icon v-else icon="square" @click="handleAllSelect"></b-icon>
              </span>
              {{ allSelected ? 'Deselect' : 'Select' }} All
            </b-col>
            <b-col v-if="cartProducts.length" style="text-align: right">
              <b-button @click="handleDeleteSelected" variant="danger" :disabled="this.selectedProducts.length === 0 || busyInDelete">
                Delete Selected
              </b-button>
            </b-col>
          </b-row>
        </b-container>
        <b-container fluid class="p-0">
          <b-container class="mb-2" v-model="selectedProducts" v-for="(product, index) in cartProducts" :key="index" :value="index">
            <!-- <pre>{{product}}</pre> -->
            <b-card class="shadow-sm">
                <b-row>
                  <b-col cols="1" class="align-self-center">
                    <div style="cursor: pointer;">
                      <b-icon v-if="isSelected(index)" scale="1.1" icon="check-square" @click="handleCartDeselect(index)"></b-icon>
                      <b-icon v-else icon="square" @click="handleCartSelect(index)"></b-icon>
                    </div>
                  </b-col>
                  <b-col cols="11" class="">
                    <b-media no-body>
                      <b-media-aside vertical-align="center">
                        <router-link :to="{ name: 'CertainProduct', params: { id: product.id } }">
                          <b-img class="d-flex img-thumbnill align-self-center" width="150" height="150" :src="product.image.link"></b-img>
                        </router-link>
                      </b-media-aside>
                      <b-media-body>
                        <b-row class="ml-1">
                          <b-col md="6" class="text-center p-0 align-self-center">
                            <span class="">
                              <router-link
                                class="text-decoration-none"
                                style="color: black"
                                :to="{ name: 'CertainProduct', params: { id: product.id } }"
                              >
                                <strong>{{product.name}}</strong><br>
                              </router-link>
                              <small v-for="(value,key,index) in product.specifications" :key="index" style="color:grey">
                                {{key}}: <b>{{value}}</b><br>
                              </small>
                            </span>
                          </b-col>
                          <b-col md="2" class="text-center p-0">
                            <span id="prev-price">
                              <strike>&#2547;{{previousPrice(product.curPrice,product.discount)}}</strike>
                              <strong id="discount" class="ml-1 d-block">{{ -product.discount }}%</strong>
                            </span>
                            <span id="cur-price">&nbsp;&nbsp;&#2547;{{product.curPrice}}</span>
                            <br>
                            <b-avatar button icon="trash" style="transform: scale(0.85,0.85)" variant="danger" class="mt-md-4" :disabled="busyInDelete"
                                      @click="handleCartRemove(index)"></b-avatar>
                          </b-col>
                          <b-col md="4" class="text-center p-0 align-self-center">
                            <div class="d-inline">
                              <b-button class="text-center mr-1 px-2" variant="white" @click="product.quantity++">+</b-button>
                              <span>  {{product.quantity}}  </span>
                              <b-button class="text-center ml-1 px-2" variant="white"
                              @click="product.quantity>1?product.quantity--:product.quantity">-</b-button>
                            </div>
                          </b-col>
                        </b-row>
                      </b-media-body>
                    </b-media>
                  </b-col>
                </b-row>
            </b-card>
          </b-container>
        </b-container>
      </b-col>
    <b-col md="4" v-if="cartProducts.length" class="mt-md-5">
      <b-card no body class="mt-md-3">
        <template v-slot:header>
          <h4 class="mb-0">Order Summary</h4>
        </template>
        <b-card-body>
          <dl class="row">
            <dt class="col-6">Subtotal({{itemCount}}/{{selectedProducts.length}} items)</dt>
            <dd class="col-6">&#2547;{{ order.subTotal }}</dd>
            <dt class="col-6">Delivery charge</dt>
            <dd class="col-6">&#2547;{{order.deliveryCharge}}</dd>
            <dt class="col-6">Total</dt>
            <dd class="col-6">&#2547;{{total}}</dd>
          </dl>
        </b-card-body>
        <template v-slot:footer>
          <div class="text-center">
            <b-overlay :show="busy1" opacity="0.4" spinner-small
                      spinner-variant="primary" class="d-inline block" @hidden="onHidden">
              <b-button id="proceedButton" @click="handleProceeding"
                :disabled="!selectedProducts.length || busy1"
                pill
              >
                Proceed to Pay
              </b-button>
            </b-overlay>
          </div>
        </template>
      </b-card>
    </b-col>
    </b-row>
    <!-- <pre>{{selectedProducts}}</pre>
    <pre>{{order}}</pre> -->
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'CartDetails',
  title: 'Bag Details',
  data () {
    return {
      // from here
      busy1: false
    }
  },
  computed: {
    ...mapGetters([
      'user',
      'authToken',
      'cartProducts',
      'selectedProducts',
      'allSelected',
      'itemCount',
      'isSelected',
      'busyInDelete'
    ]),
    order () {
      const order = {
        products: [],
        deliveryCharge: this.user.district === 'Bogura' ? 0 : 60,
        subTotal: 0
      }
      for (const idx of this.selectedProducts) {
        if (idx < this.cartProducts.length) {
          const product = this.cartProducts[idx]
          order.products.push(product)
          order.subTotal += product.curPrice * (1 + product.vat / 100) * product.quantity
        }
      }
      order.subTotal = Math.round(order.subTotal)
      return order
    },
    total () {
      return this.order.subTotal ? this.order.deliveryCharge + this.order.subTotal : 0
    }
  },
  methods: {
    ...mapActions([
      'cartRemove',
      'loadCart',
      'handleCartSelect',
      'handleCartDeselect',
      'handleAllSelect',
      'handleAllDeselect',
      'handleCartRemove',
      'handleDeleteSelected'
    ]),
    onHidden () {
      this.$refs.button.focus()
    },
    previousPrice (curPrice, discount) {
      return Math.round(curPrice / (1 - discount / 100))
    },
    handleProceeding () {
      if (!this.authToken) {
        this.$bvModal.show('login-modal')
      } else {
        this.busy1 = true
        this.$router.push({
          name: 'OrderSubmitDetails',
          query: {
            order: this.order,
            removed: {
              indices: this.selectedProducts
            }
          }
        })
      }
    }
  },
  created () {
    this.loadCart()
  }
}
</script>

<style scoped>
    #imageProduct {
        width: 100%;
        max-width:50px;
        max-height:50px;
        height: auto;
        border: 1px solid black;
    }
     #prev-price {
        color: grey;
    }
    #discount {
        color: red;
    }
    #cur-price {
        font-size: 120%;
        color: darkorange;
    }
    #proceedButton {
      width: 100%;
      background-color: #ff4747;
    }
    .card-body {
      padding-top: 1%;
      padding-bottom: 1%;
    }
</style>
