<template>
<div>
  <b-overlay :show="!isDone" spinner-large
             spinner-variant="primary" spinner-type="grow" rounded="sm" style="min-height:500px">
    <div v-if="orders.length">
      <b-container fluid>
        <b-toast id="my-toast" :variant="isPossible ? 'info':'warning'">
          <template v-slot:toast-title>
            <div class="ml-auto text-center">
              <b-img blank :blank-color="isPossible ? '#008000':'#ff5555'" class="" width="12" height="12"></b-img>
              <strong class="ml-1" v-if="isPossible">Success!!</strong>
              <strong class="ml-1" v-else>Error!!</strong>
            </div>
          </template>
          <p class="text-center">
            <b-spinner type="grow" small class="mr-3"></b-spinner>{{errorMessage}}
            <b-spinner type="grow" small class="ml-3"></b-spinner>
          </p>
        </b-toast>
        <h1 class=" my-2">Order History</h1>
        <hr><hr>
        <b-row>
          <b-overlay :show="busyReload" opacity="0.4" spinner-small
                     spinner-variant="primary" class="d-inline block mx-3">
            <b-button @click="reloadAllCurrentOrders" variant="success" style="float:left;">Reload</b-button>
          </b-overlay>
          <b-form @submit="clicking(goToOrderID)" class="ml-auto mr-3" style="float: right;width:400px;">
            <b-input-group>
              <b-form-input
                id="input-1"
                v-model="goToOrderID"
                type="number"
                :min="10000"
                placeholder="Go to order"
              ></b-form-input>
              <b-input-group-append>
                <b-button type="submit" variant="success">Go</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form>
        </b-row>
        <hr>
        <b-table :items="orders" :fields="fields" striped responsive="sm" class="mx-3">
          <template v-slot:cell(show_details)="row">
            <router-link :to="{ name: 'CertainOrder', params: { id: row.item.orderID } }">
              <b-button size="sm" class="mr-2" variant="success" >
                Show Details
              </b-button>
            </router-link>
          </template>
          <template v-slot:cell(order_date)="row">
            {{ convertToDate(row.item.ordered._seconds) }}
          </template>
          <template v-slot:cell(status)="row">
            {{ row.item.currentStatus.name }}<br>
            {{ convertToDate(row.item.currentStatus.date._seconds) }}
          </template>
          <template v-slot:cell(total)="row">
            &#2547;{{ row.item.total }}
          </template>
          <template v-slot:cell(options)="row">
            <!-- <b-container fluid class="d-flex justify-content-center"> -->
            <b-overlay :show="busy" opacity="0.4" spinner-small v-if="row.item.currentStatus.name==='delivered'"
                       spinner-variant="primary" class="d-inline block">
              <b-button size="sm" class="mr-2" :disabled="busy" variant="warning"  @click="requestRefund(row.item)">
                Request Refund
              </b-button>
            </b-overlay>
            <b-overlay :show="busy" opacity="0.4" spinner-small v-if="row.item.currentStatus.name==='ordered'"
                       spinner-variant="primary" class="d-inline block">
              <b-button size="sm" :disabled="busy" variant="danger" @click="cancelOrder(row.item.orderID)">
                Cancel Order
              </b-button>
            </b-overlay>
            <!-- </b-container> -->
          </template>
        </b-table>
        <b-container class="d-flex justify-content-center">
          <b-overlay :show="busy1" opacity="0.4" spinner-small
                     spinner-variant="primary" class="d-inline block">
            <b-button :disabled="busy1" style="background-color: #007bff" v-if="!orderLoadingDone" @click="loadOrders">
              Load More
            </b-button>
          </b-overlay>
        </b-container>
        <hr>
      </b-container>
    </div>
  </b-overlay>
  <div class="mx-auto shadow-lg bg-info text-center col-sm-8" v-if="isDone && !orders.length">
    <b-card no-body bg-variant="info" class="my-5" text-variant="white">
        <template v-slot:header>
          <p class="text-center">
            <b-spinner type="grow" variant="warning" small class="mr-3"></b-spinner>Attention!!
            <b-spinner type="grow" variant="warning" small class="ml-3"></b-spinner>
          </p>
        </template>
        <b-card-body>
          <p class="text-center">There is no order to show</p>
          <p class="text-center">Enjoy shopping with Azraar</p>
        </b-card-body>
      </b-card>
  </div>
</div>
</template>

<script>
import { mapGetters } from 'vuex'
import api from '../../../plugins/axios'

export default {
  name: 'OrderShower',
  title: 'My Order History',
  data () {
    return {
      // fields: ['orderID', 'order_date', 'Total', 'status', 'show_details', 'event_log', 'refund'],
      fields: ['orderID', 'order_date', 'Total', 'status', 'show_details', 'options'],
      orders: [],
      orderLoadingDone: true,
      image: {
        link: 'https://static-01.daraz.com.bd/p/7883ac9ce3be09d4e7e9e44f139e8d68.jpg'
      },
      errorMessage: '',
      isPossible: '',
      busy: false,
      busy1: false,
      busy2: false,
      busyReload: false,
      goToOrderID: null,
      isDone: false
    }
  },
  computed: {
    ...mapGetters([
      'getConfig',
      'user',
      'userLoaded'
    ])
  },
  methods: {
    loadOrders () {
      console.log('in load orders')
      if (!this.orderLoadingDone) this.busy1 = true
      const offset = this.orders.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      api.post('/orders/all', {
        phoneNo: this.user.phoneNo
      }, this.getConfig({ offset, limit }, null))
        .then(response => {
          this.orders = this.orders.concat(response.data.orders)
          this.isDone = true
          this.busy1 = false
          this.orderLoadingDone = response.data.orders.length < limit
          console.log(response)
        })
        .catch(() => {
          this.$notification.error('Failed to load orders')
        })
    },
    reloadAllCurrentOrders () {
      if (!this.orderLoadingDone) this.busyReload = true
      const offset = 0
      const limit = this.orders.length || process.env.VUE_APP_PAGE_LIMIT
      api.post('/orders/all', {
        phoneNo: this.user.phoneNo
      }, this.getConfig({ offset, limit }, null))
        .then(response => {
          this.orders = response.data.orders
          this.busyReload = false
          this.orderLoadingDone = response.data.orders.length < limit
        })
        .catch(() => {
          // this.busyReload = false
          this.$notification.error('Failed to reload orders')
        })
    },
    clicking (ID) {
      if (ID) {
        this.$router.push({
          name: 'CertainOrder',
          params: {
            id: ID
          }
        })
      }
    },
    requestRefund (order) {
      this.busy = true
      const orderID = order.orderID
      api.post('/order/refund', {
        phoneNo: this.user.phoneNo,
        orderID
      }, this.getConfig())
        .then(response => {
          order.currentStatus = new Date(response.data.currentStatus) // new Date() is needed here
          this.busy = false
          this.isPossible = true
          this.message = 'refund requested for order ' + orderID
          this.$notification.success(`Refund requested for order-${orderID}`)
        })
        .catch(error => {
          this.busy = false
          this.isPossible = false
          this.errorMessage = error.response.data.error
          this.$bvToast.show('my-toast')
          this.$notification.error(`Failed to request refund for order-${orderID}`)
        })
    },
    cancelOrder (orderID) {
      this.busy = true
      api.delete('/order/cancel', this.getConfig(null, {
        phoneNo: this.user.phoneNo,
        orderID
      }))
        .then(response => {
          this.busy = false
          this.$notification.success(`Canceled order-${orderID}`)
          this.orders = this.orders.filter(order => order.orderID !== orderID)
        })
        .catch(() => {
          this.busy = false
          this.$notification.error(`Failed to cancel order-${orderID}`)
        })
    },
    previousPrice (curPrice, discount) {
      return Math.round(curPrice / (1 - discount / 100))
    },
    convertToDate (timestamp) {
      const unixTimestamp = timestamp
      const milliseconds = unixTimestamp * 1000
      const dateObject = new Date(milliseconds)
      const dateFormat = dateObject.toLocaleString()
      return dateFormat
    },
    getDiff (timestamp, curStatus) {
      const presentDate = new Date()
      const previousDate = this.convertToDate(timestamp)
      const difference = presentDate.getTime() - previousDate
      return (curStatus === 'delivered' && difference < 7)
    }
  },
  created () {
    if (this.userLoaded) {
      this.loadOrders()
    } else {
      const inCreated = setInterval(() => {
        if (this.userLoaded) {
          this.loadOrders()
          clearInterval(inCreated)
        }
      }, process.env.VUE_APP_RETRY_INTERVAL)
    }
  }
}
</script>

