<template>
  <b-container>
    <b-overlay :show="!orderLoaded" spinner-large
               spinner-variant="primary" spinner-type="grow" rounded="sm" style="min-height:500px">
      <div v-if="orderLoaded">
        <h1 class="my-3">Order ID: {{$route.params.id}}</h1>
        <hr><hr>
        <h2 class="my-3">Products of this order:</h2>
        <b-card class="mb-2 shadow" v-for="(product, index) in order.products" :key="index" :value="index">
          <!-- <b-container class="shadow" fluid> -->
          <b-media no-body>
            <router-link :to="{ name: 'CertainProduct', params: { id: product.id } }">
              <b-media-aside vertical-align="center">
                <b-img class="d-flex img-thumbnill align-self-center" width="150" height="150" :src="product.image.link" :alt="product.image.name"></b-img>
              </b-media-aside>
            </router-link>
            <b-media-body class="my-auto text-center">
              <b-row>
                <b-col md="6" class="my-auto text-center" align-self="center">
                      <span class="">
                        <router-link
                          class="text-decoration-none"
                          style="color: black"
                          :to="{ name: 'CertainProduct', params: { id: product.id } }"
                        >
                          <strong>{{product.name}}</strong><br>
                        </router-link>
                        <small v-for="(value,key,index) in product.specifications" :key="index" style="color:grey">
                          {{key}}: <b>{{value}}</b><br>
                        </small>
                      </span>
                </b-col>
                <b-col md="2" class="my-auto text-center">
                      <span id="prev-price">
                        <strike>&#2547;{{previousPrice(product.curPrice,product.discount)}}</strike>
                        <strong id="discount" class="ml-1 d-block">{{ -product.discount }}%</strong>
                      </span>
                  <span id="cur-price">&nbsp;&nbsp;&#2547;{{product.curPrice}}</span>
                  <br>
                </b-col>
                <b-col md="4" align-self="center" class="text-center">
                  <p>{{product.quantity}}x</p>
                  <b-form-rating inline no-border v-if="product.hasOwnProperty('rating')"
                                 readonly v-model="product.rating"  variant="warning"></b-form-rating>
                  <b-button v-b-modal="'ratingModal'+index+order.orderID"  variant="success"
                            v-if="!product.rating
                       && order.currentStatus.name!=='ordered' && order.currentStatus.name!=='confirmed'">Rate it</b-button>
                  <b-modal :id="'ratingModal'+index+order.orderID" ref="modal" title="Submit Your Rating" ok-title="Submit"
                    @ok="rateProduct(order.orderID,index,product)"
                    :ok-disabled="!product.localRating || (Boolean(product.localReview) && product.localReview.length > maxReviewLength)">
                    <!-- <label for="rate">Submit Your Rating</label> -->
                    <b-form-rating id="rate" v-model="product.localRating"></b-form-rating>
                    <br>
                    <b-form-textarea
                      id="review-area"
                      v-model="product.localReview"
                      placeholder="Write a review..."
                      rows="3"
                      max-rows="4"
                      no-resize
                      @keypress="clip($event, product.localReview, maxReviewLength)"
                      trim
                    ></b-form-textarea>
                    <small
                      v-if="product.localReview && product.localReview.length >= maxReviewLength"
                      class="float-right"
                      style="color: red;"
                    >
                      Reached limit of {{maxReviewLength}} characters
                    </small>
                  </b-modal>
                </b-col>
              </b-row>
            </b-media-body>
          </b-media>
          <!-- </b-container> -->
        </b-card>
        <hr>
        <h2>Event Log:</h2>
        <b-table :items="order.status" :fields="fields">
          <template v-slot:cell(Event_Name)="row">
                  {{row.item.name}}
              </template>
              <template v-slot:cell(Date)="row">
                  {{convertToDate(row.item.date._seconds)}}
              </template>
              <template v-slot:cell(Comment)="row">
                  {{row.item.comment ? row.item.comment : 'No Comments'}}
              </template>
          </b-table>
        <hr>
        <div class="my-3">
          <b-overlay :show="busy" opacity="0.4" spinner-small v-if="order.currentStatus.name==='delivered'"
                     spinner-variant="primary" class="d-inline block">
            <b-button class="mr-2" :disabled="busy" variant="warning"  @click="requestRefund(order)">
              Request Refund
            </b-button>
          </b-overlay>
          <b-overlay :show="busy" opacity="0.4" spinner-small v-if="order.currentStatus.name==='ordered'"
                     spinner-variant="primary" class="d-inline block">
            <b-button :disabled="busy" variant="danger" @click="cancelOrder(order.orderID)">
              Cancel Order
            </b-button>
          </b-overlay>
        </div>
      </div>
    </b-overlay>
  </b-container>
</template>
<script>
import loader from '../../../mixins/loader'
import inputClipper from '../../../mixins/inputClipper'
import api from '../../../plugins/axios'
import { mapGetters } from 'vuex'

export default {
  name: 'CertainOrder',
  title () {
    return 'Order-' + this.$route.params.id
  },
  data () {
    return {
      fields: ['Event_Name', 'Date', 'Comment'],
      orders: ['Ordered', '234', 'was ordered'],
      orderLoaded: false,
      order: {},
      maxReviewLength: 500,
      busy: false
    }
  },
  computed: {
    ...mapGetters([
      'user',
      'userLoaded',
      'getConfig'
    ])
  },
  mixins: [
    loader,
    inputClipper
  ],
  methods: {
    convertToDate (timestamp) {
      const milliseconds = timestamp * 1000
      const dateObject = new Date(milliseconds)
      return dateObject.toLocaleString()
    },
    loadOrder () {
      const orderID = this.$route.params.id
      api.post('/order/get', {
        phoneNo: this.user.phoneNo
      }, this.getConfig({ orderID }))
        .then(response => {
          this.order = response.data
          this.orderLoaded = true
        })
        .catch(error => {
          this.orderLoaded = true
          this.$notification.warning('Invalid order access')
          if (error.response.data.error === 'Invalid order access') {
            this.$router.push({ name: 'OrderShower' })
          }
        })
    },
    rateProduct (orderid, index, product) {
      const rating = product.localRating
      const review = product.localReview
      api.post('/order/rate', {
        phoneNo: this.user.phoneNo,
        orderID: Number(orderid),
        orderIndex: Number(index),
        rating,
        review
      }, this.getConfig())
        .then(response => {
          product.rating = rating
          product.localRating = null
          product.localReview = null
          this.$notification.success(`Product-${product.id} rated ${rating}-stars`)
        })
        .catch(() => {
          this.$notification.error(`Rating product-${product.id} failed`)
        })
    },
    requestRefund (order) {
      this.busy = true
      const orderID = order.orderID
      api.post('/order/refund', {
        phoneNo: this.user.phoneNo,
        orderID
      }, this.getConfig())
        .then(response => {
          order.currentStatus = new Date(response.data.currentStatus) // new Date() is needed here
          this.busy = false
          this.$notification.success(`Refund requested for order-${orderID}`)
        })
        .catch(error => {
          this.busy = false
          this.$notification.error(error.response.data.error)
        })
    },
    cancelOrder (orderID) {
      this.busy = true
      api.delete('/order/cancel', this.getConfig(null, {
        phoneNo: this.user.phoneNo,
        orderID
      }))
        .then(response => {
          this.busy = false
          this.$notification.warning(`Canceled order-${orderID}`)
          this.$router.push({ name: 'OrderShower' })
        })
        .catch(() => {
          this.busy = false
          this.$notification.error(`Failed to cancel order-${orderID}`)
        })
    },
    previousPrice (curPrice, discount) {
      return Math.round(curPrice / (1 - discount / 100))
    },
    initialLoader () {
      if (this.userLoaded) {
        this.loadOrder()
      } else {
        const inCreated = setInterval(() => {
          if (this.userLoaded) {
            this.loadOrder()
            clearInterval(inCreated)
          }
        }, process.env.VUE_APP_RETRY_INTERVAL)
      }
    }
  },
  watch: {
    '$route' (to, from) {
      this.initialLoader()
    }
  },
  created () {
    this.initialLoader()
  }
}
</script>
